@font-face {
    font-family: "Raleway";
    src: url("./Raleway-Bold.ttf") format("truetype");
    font-style: normal;
    font-weight: normal;
}

.header {
    position: relative;
    width: 100%;
    max-width: 100%; /* Растягиваем на весь экран */
    margin: 0 auto;
    padding: 20px 40px; /* Добавляем отступы для кнопок */
    
	color:white;
}

.carousel {
    margin-top: 60px;
    position: relative;
    width: 100%;
    overflow: hidden; /* Убедитесь, что содержимое не выходит за пределы контейнера */
    background-color: rgb(9, 255, 0); /* Явно устанавливаем фон карусели */
}

.carousel-wrapper {
    display: flex;
    overflow: hidden; /* Скрывает лишнее содержимое */
    background-color: rgb(0, 255, 213); /* Явно устанавливаем фон обертки карусели */
}

.carousel-content {
    display: flex;
    transition: transform 0.5s ease;
    width: 100%;
}

.carousel-item {
    min-width: 100%;
    box-sizing: border-box;
    padding: 20px;
    text-align: center;
    background-color: rgb(48, 2, 253); /* Явно устанавливаем фон для карточек карусели */
}

.carousel-item img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
    margin-bottom: 10px;
}

.carousel-item h2 {
    font-family: "Raleway", sans-serif;
    font-size: 1.5rem;
    margin: 10px 0;
}

.carousel-item p {
    font-family: "Raleway", sans-serif;
    font-size: 1rem;
}

.carousel-button {
    background: rgba(0, 0, 0, 0.5);
    border: none;
    color: #fff;
    cursor: pointer;
    font-size: 1.5rem;
    width: 50px; /* Ширина кнопки */
    height: 50px; /* Высота кнопки */
    border-radius: 50%; /* Сделать кнопку круглой */
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    transition: background 0.3s, transform 0.3s; /* Плавное изменение фона и анимация при наведении */
}

.carousel-button:hover {
    background: rgba(0, 0, 0, 0.8);
    transform: translateY(-50%) scale(1.1); /* Увеличение кнопки при наведении */
}

.carousel-button.prev {
    left: 20px; /* Расстояние от левого края */
}

.carousel-button.next {
    right: 20px; /* Расстояние от правого края */
}

/* Медиазапросы для адаптивности */
@media (min-width: 768px) {
    .carousel-item img {
        max-width: 90%; /* Уменьшите максимальную ширину изображений */
    }
}

@media (min-width: 1200px) {
    .header {
        padding: 40px 60px; /* Увеличьте отступы для больших экранов */
    }

    .carousel-button {
        font-size: 2rem; /* Увеличьте размер текста на кнопках */
        width: 60px; /* Увеличьте размер кнопок */
        height: 60px;
    }
}
